.input:focus {
}

.input {
    display: flex;
    width: 372px;
    height: 36px;
    justify-content: center;
    align-items: center;


    background: #FFFFFF;
    border: 1px solid #D9D9D9;
    box-sizing: border-box;
    border-radius: 5px;
}

.inputWrapper {
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
}

.superInput {
}

.errorInput {

    color: #CC1439;
    font-family: 'Montserrat', serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: left;

    border: 1px solid #CC1439;
    border-radius: 5px;
}

.errorInput:focus {
    color: #CC1439;
    font-family: 'Montserrat', serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: left;

    border: 1px solid #CC1439;
    border-radius: 5px;
}

.error {
    color: #CC1439;
    font-family: Montserrat,serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
